define('skylark-domx-medias/medias',[
    "skylark-langx/skylark",
    "skylark-langx/langx",
    "skylark-domx-data"
], function(skylark,langx,datax) {
      function medias() {
         return medias;
      }

     langx.mixin(medias,{

        muted : function(media) {
            return media.muted;
        },

        paused : function(media) {
            return media.paused;
        },

        played : function(media) {
            return media.played;
        },

        load : function(media) {
          return media.load();
        },

        play : function() {
          return media.play();
        },

        stop : function() {
          return media.stop();
        },

        pause : function() {
          this._video.pause();      
        }


     });


      return skylark.attach("domx.medias" , medias);
  
});

define('skylark-domx-medias/main',[
    "skylark-domx-velm",
    "skylark-domx-query",
	"./medias"
],function(velm,$,medias){

   // from ./medias
    velm.delegate([
        "muted",
        "paused",
        "played",
        "load",
        "play", 
        "stop",
        "pause",
        "height",
        "marginExtents",
        "marginRect",
        "marginSize",
        "offsetParent",
        "paddingExtents",
        "pagePosition",
        "pageRect",
        "relativePosition",
        "relativeRect",
        "scrollIntoView",
        "scrollLeft",
        "scrollTop",
        "pageSize",
        "width"
    ], medias,{
    });

	return medias;
});
define('skylark-domx-medias', ['skylark-domx-medias/main'], function (main) { return main; });

